.place-order{
    display: flex;
    align-items: start;
    justify-content: space-between;
    gap: 50px;
    margin-top: 100px;
}

.place-order-left{
    width: 100%;
    max-width: max(30%,500px);
}

.place-order-left .title {
    font-size: 30px;
    font-weight: 600;
    margin-bottom: 50px;
}

.place-order-left input{
    width: 100%;
    margin-bottom: 15px;
    padding: 10px;
    border: 1px solid #c5c5c5;
    outline-color: tomato;
    border-radius: 4px;
}

.place-order-left .multi-fields{
    display: flex;
    gap: 10px;
}

.place-order-right{
    width: 100%;
    max-width: max(40%, 500px);
}

.place-order-right .cart-total button{
    display: block;
    margin: 30px auto;
}

@media (max-width: 768px) {
    .place-order{
        flex-direction: column;
    }
}