.explore-menu{
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.explore-menu h2{
    font-weight: bold;
    color: #262626;
}

.explore-menu-text{
    max-width: 50%;
    color: #808080;
}


.explore-menu-list{
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap:30px;
    overflow-x: scroll;
    text-align: center;
    margin: 20px 0;
}

.explore-menu-list::-webkit-scrollbar{
    display: none;
}

.explore-menu-list-item img{
    width: 7.5vw;
    min-width: 80px;
    cursor: pointer;
    border-radius: 50%;
    transition: 0.2s linear;
}

.explore-menu-list-item p{
    margin-top: 10px;
    cursor: pointer;
    font-size: max(1.5vw, 16px);
    color: #747474;
}

.explore-menu hr{
    height: 2px;
    border: none;
    margin: 10px 0;
    background-color: tomato;
}

.explore-menu-list-item .active{
    border: 4px solid tomato;
    padding: 2px;
}

@media (max-width: 1050px) {
    .explore-menu-text{
        max-width: 100%;
        font-size: 14px;
    }
}