.cart{
    margin-top: 100px;
}

.cart-items-title{
    display: grid;
    grid-template-columns: 1fr 1.5fr 1fr 1fr 1fr 0.5fr;
    align-items: center;
    color: grey;
    font-size: max(1vw, 20px);
}



.cart-items-item{
    margin: 10px 0;
    color: black;
}

.cart-items-item img{
    width: 50px;
}

.cart hr{
    height: 1px;
    background-color: #e2e2e2;
    border: none;
}

.cart-items-item .cross{
    color: red;
    cursor: pointer;
}

.cart-bottom{
    display: flex;
    justify-content: space-between;
    gap: max(12vw, 20px);
    margin-top: 80px;
}

.cart-total{
    flex: 1;
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.cart-total h2{
    font-weight: 600;
}

.cart-total-details{
    display: flex;
    justify-content: space-between;
    color: #555;
}

.cart-total hr{
    margin: 10px 0;
}

.cart-total-details b{
    font-weight: bold;
}

.cart-total button{
    border: none;
    color: white;
    background-color: tomato;
    padding: 12px 0;
    border-radius: 4px;
    cursor: pointer;
    width: max(15vw, 200px);
    display: block;
    margin: auto;
}

.cart-promocode{
    flex: 1;
}

.cart-promocode div p{
    color: #555;
}

.cart-promocode-input{
    margin-top: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #eaeaea;
    border-radius: 4px;
}

.cart-promocode-input input{
    background: transparent;
    border: none;
    outline: none;
    padding-left: 10px;
}

.cart-promocode-input button{
    width: max(10vw, 150px);
    padding: 12px 5px;
    background-color: black;
    color: white;
    border: none;
    border-radius: 4px;
}

@media (max-width:750px){
    .cart-bottom{
        flex-direction: column-reverse;
    }

    .cart-promocode{
        justify-content: start;
    }

    .cart-items-title p{
        font-size: 3vw;
    }

    .cart-items-title p:nth-child(5){
        text-align: center;
    }

    .cart-items-item p:nth-child(5){
        text-align:left;
    }
}