.app-download{
    margin: auto auto;
    margin-top: 100px;
    font-size: max(2vw, 20px);
    text-align: center;
    font-weight: 500;
}

.app-download-platforms{
    display: flex;
    justify-content: center;
    gap: max(2vw, 10px);
    margin-top: 40px;
}

.app-download-platforms img{
    width: max(30vw,120px);
    max-width: 180px;
    cursor: pointer;
    transition: 0.3s;
}

.app-download-platforms img:hover{
    transform: scale(1.1);
}